@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&family=Noto+Sans+KR:wght@400;500&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Pacifico&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.page {
  @apply bg-green-200 flex flex-col pb-2;
}
.card {
  @apply bg-white rounded-2xl overflow-hidden shadow-md;
}

.cardItems {
  @apply flex flex-col gap-3 p-3;
}

.centerPage {
  @apply flex flex-col items-center;
}

.centerItem {
  @apply flex justify-center items-center;
}

.submenu {
  @apply text-xl font-bold mx-5 my-3;
}

.wrapper {
  @apply text-xl;
}

.input {
  @apply w-[calc(100%-(mx*3))] h-6 mx-3 my-2 px-2 py-1 text-sm shadow-sm rounded-md outline-none;
}
.loginInput {
  @apply w-full h-8 px-2 py-1 text-sm rounded-sm outline-main border-2 border-gray-200;
}
.textArea {
  @apply w-full px-2 py-1 text-sm rounded-sm outline-main border-2 border-gray-200 resize-none;
}
/* .textArea {
  @apply mx-3 my-2 px-2 py-1 resize-none outline-none rounded-md text-sm;
} */
.ratingButton {
  @apply w-1/5 h-full bg-gray-200 hover:bg-sub;
}
tr {
  @apply h-6;
}
td:nth-child(odd) {
  @apply w-14 h-full text-xs align-middle text-start pl-2;
}
td:nth-child(even) {
  @apply w-auto pl-2 border-slate-300 border-b;
}
.ellipsis {
  @apply whitespace-nowrap overflow-hidden text-ellipsis;
}
